.Speaker {
    display: grid;
    grid-template-columns: 50% 50%;
    margin: 2em 0;
    border: 1px solid #888;
    border-radius: 5px;
}

.Speaker-info {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;

    padding: 0.5em 0.5em 0.5em 0.5em;
    color: #888;
    border-bottom: 1px solid #ccc;
}

.Speaker-info .Speaker-name {
    font-size: 2rem;
}
.Speaker-info .Speaker-model_name {
    float: right;
    font-size: 1rem;
}

.Speaker-volume {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;

    padding: 0 0.1em;
    font-size: 10rem;
    font-weight: bold;

    text-align: center;
}

button {
    background-color: transparent;
    border: none;
}

.Speaker-switch_to_tv {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;

    padding: 0.9em;
    color: #888;

    border-top: 1px solid #ccc;
    text-align: center;
}

.Speaker-volume_control {
    font-size: larger;
    border-top: 1px solid #ccc;
    padding: 0.5em;
}

.Speaker-down {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 4;
    grid-row-end: 5;

    border-right: 1px solid #ccc;
}

.Speaker-up {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 4;
    grid-row-end: 5;
}



